.circle-unread {
  position: relative;
  display: flex;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #3369f3;
  overflow: hidden;

  &__wrapper {
    position: absolute;
    top: 3px;
    right: 3px;
    width: 14px;
    height: 14px;
  }

  &__text {
    display: flex;
    justify-content: center;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #ffffff;
    margin: 0;
    padding-top: 1px;
    // padding-left: 1px;
  }
}
