.page {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  height: 100vh;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-style: normal;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
p {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding: 0;
  color: #999999;
}
